import {commumal} from "../public";
var predown={
    methods:{
        init(){
            $(".seledate span").on("click",commumal.spanSelect);
            $(".download").on("click",predown.events.predown);
            $(".sendEmail").on("click",predown.events.email)
        }
    },
    events:{
        predown(){
            var that=$(".seledate span.seleted")
            var startDt=that.attr("data-start")
            var endDt=that.attr("data-end")
            $.defs.loadingshow();
            $.defs.myPost({
                START_DT:startDt,
                END_DT:endDt,
            },"../file/PepairReportExps").then((data)=>{
                $.defs.loadinghide();
                 if(data.RETURN_CODE==0){
                     location=data.href;
                 }else{
                    $.defs.msg("报销单生成失败")
                 }
            })
        },
        email(){
            var that=$(".seledate span.seleted")
            var startDt=that.attr("data-start")
            var endDt=that.attr("data-end");
            $.defs.loadingshow("正在发送中");
            $.defs.myPost({
                START_DT:startDt,
                END_DT:endDt,
                EMAIL:"Y"   //此参数在于区分是报销单下载还是发送邮箱
            },"../file/PepairReportExps").then((data)=>{
                $.defs.loadinghide(); 
                if(data.RETURN_CODE==0){
                    weui.toast('发送成功', 3000);
                }else{
                    $.defs.msg("发送失败")
                }
            })
        }
    },
    config:{
        init(){
            $(".top .huan").text("其他>报销单下载")
            commumal.loadDate();
        }
    }
}
$(function(){
    predown.config.init();
    predown.methods.init()
})


